<template>
  <div class="login-component-container">
    <div class="Login-content">
      <h1 id="login-content-title">{{ title }}</h1>
      <p class="login-content-desc">{{ desc }}</p>
      <div
        class="login-contentform"
        :style="{ display: 'flex', gap: '1.5rem' }"
      >
        <div class="input-container">
          <input
            v-model="input"
            type="text"
            class="phone-number"
            placeholder="09xxxxx6789"
            pattern="09[0-9]{9}"
          />
          <div class="clear-input" ref="clearInput" @click="clearInputValue">
            •
          </div>
        </div>
        <button
          id="send-link"
          :style="{
            padding: '0.25rem 0.5rem',
            borderRadius: '0.25rem',
            outline: 'none',
            border: 'none',
            cursor: 'pointer',
            width: '8rem',
            color: '#21aa58',
            fontWeight: 'bolder',
          }"
          style="@media screen and (max-width: 1024px) {width: 6rem}"
        >
          ارسال لینک
        </button>
      </div>
      <div class="login-appbtn">
        <button class="login-toapp" v-if="width > 840">
          ورود به اپلیکیشن اسنپ
        </button>
        <button class="login-toapp" v-else @click="openPopUp">
          دانلود اپلیکیشن اسنپ
        </button>
      </div>
    </div>
    <div class="login-image"></div>
    <div class="popup" ref="popup">
      <div class="close-btn" @click="closePopup">×</div>
      <Download />
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { watch } from "vue";
import Download from "../Download/Download.vue";
export default {
  components: {
    Download,
  },
  setup() {
    let title = ref(
      "تجربه زندگی راحت‌تر، سریع‌تر و به صرفه‌تر با سوپر اپلیکیشن اسنپ!"
    );
    let desc = ref(
      "از درخواست خودرو گرفته تا سفارش غذا، خرید سوپر مارکتی، خرید بلیت سفر، مشاوره پزشکی و روانشناسی، رزرو هتل و... را میتوانید با سوپر اپلیکیشن اسنپ انجام دهید."
    );
    let input = ref("");
    let clearInput = ref(null);
    const popup = ref(null);
    const width = ref(0);
    const popUpIsOpen = ref(false);
    window.addEventListener("resize", () => {
      width.value = window.innerWidth;
    });

    watch(input, () => {
      if (!input.value) {
        // •
        clearInput.value.innerHTML = "•";
        clearInput.value.style.fontSize = "30px";
        clearInput.value.style.top = "0.15rem";
      } else {
        clearInput.value.innerHTML = "x";
        clearInput.value.style.fontSize = "20px";
        clearInput.value.style.top = "-0.01rem";
      }
    });
    function clearInputValue() {
      input.value = "";
    }
    let openPopUp = () => {
        popup.value.style.display = "flex";
        popUpIsOpen.value = true
    };
    const closePopup = () => {
      popup.value.style.display = "none";
      popUpIsOpen.value = false
    }
    return {
      title,
      desc,
      input,
      clearInput,
      clearInputValue,
      width,
      openPopUp,
      popup,
      closePopup
    };
  },
};
</script>

<style scoped>
.login-component-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background: #21aa58;
  /* position: relative; */
}
.login-image {
  background: url("../../images/main page picture/intro_desktop.jpg")
    center/cover;
}
.Login-content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 2rem 2rem 1rem;
  line-height: 3rem;
  gap: 1.5rem;
}
.Login-content h1 {
  width: 75%;
  color: #fff;
  font-weight: bolder;
  font-size: 2.35rem;
}
.Login-content p.login-content-desc {
  width: 75%;
  color: #fff;
  font-size: 1.1rem;
}
.login-contentform {
  width: 75%;
  display: flex;
  justify-content: flex-start;
}
.input-container {
  border-bottom: 2px solid #fff;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
  width: 50%;
}
.Login-content .phone-number {
  display: inline-block;
  color: #fff;
  background: none;
  border: none;
  outline: none;
  padding: 0.5rem;
  direction: ltr;
}
.clear-input {
  width: fit-content;
  height: fit-content;
  position: relative;
  top: 0.15rem;
  cursor: pointer;
  color: #fff;
  font-size: 30px;
}
.Login-content .phone-number::placeholder {
  color: #ffffff90;
}
.login-appbtn {
  width: 75%;
  padding: 1rem 0;
  display: flex;
  justify-content: flex-start;
}
.login-toapp {
  padding: 0.5rem 2.5rem;
  font-size: 1.05rem;
  font-weight: bolder;
  color: #fff;
  background: transparent;
  border: 1px solid #fff;
  border-radius: 0.25rem;
  outline: none;
  cursor: pointer;
  transition: 0.15s linear;
}
.login-toapp:hover {
  background-color: #ffffff10;
}
.popup {
  position: fixed;
  width: 100%;
  top: 53.5vh;
  background-color: #fff;
  border-radius: 0.75rem;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  display: none;
}
.close-btn{
    position: absolute;
    left: 1rem;
    top: 0.5rem;
    font-size: 1.5rem;
    display: flex;
    justify-content: flex-end;
    cursor: pointer;
    z-index: 5;
}
@media screen and (max-width: 1024px) {
  #send-link {
    width: 6rem !important;
  }
}
</style>