<script>
import { ref } from "vue";
export default {
  setup(props , {emit}) {
    const container = emit("container")
    console.log(container)
    let isOpen = ref(false);
    let navline0 = ref(null);
    let navline1 = ref(null);
    let navline2 = ref(null);
    let appItems = ref(null);
    let arrow = ref(null);
    let display = ref(false);
    const submenu = ref(null)
    let menu = () => {
      isOpen.value = !isOpen.value;
      if (isOpen.value) {
        navline1.value.style.display = "none";
        navline0.value.style.transform = "rotate(50deg) translate(5px, 5px)";
        navline2.value.style.transform = "rotate(-50deg) translate(-2px, 0px)";
        submenu.value.style.opacity = "100%";
        submenu.value.style.height = "88.5vh";
      } else {
        navline1.value.style.display = "block";
        navline0.value.style.transform = "rotate(0) translate(0)";
        navline2.value.style.transform = "rotate(0) translate(0)";
        submenu.value.style.opacity = "0"
        submenu.value.style.height = "0";
      }
    };
    let openDownloadLink = () => {
      if (!display.value) {
        appItems.value.style.display = "flex";
        arrow.value.style.transform = "rotate(90deg)";
        display.value = true;
      } else {
        appItems.value.style.display = "none";
        arrow.value.style.transform = "rotate(-90deg)";
        display.value = false;
      }
    };
    
    return {
      menu,
      isOpen,
      navline0,
      navline1,
      navline2,
      appItems,
      openDownloadLink,
      arrow,
      submenu,
    };
  },
};
</script>

