<template>
  <swiper
    :spaceBetween="30"
    :centeredSlides="true"
    :autoplay="{
      delay: 4000,
      disableOnInteraction: false,
    }"
    :pagination="{
      clickable: true,
    }"
    :navigation="false"
    :modules="modules"
    class="mySwiper"
  >
    <swiper-slide>
        <img src="../../images/slider/Food.jpg" alt="">
    </swiper-slide>
    <swiper-slide>
        <img src="../../images/slider/Market.jpg" alt="">
    </swiper-slide>
    <swiper-slide>
        <img src="../../images/slider/Driver.jpg" alt="">
    </swiper-slide>
    <swiper-slide>
        <img src="../../images/slider/Club.jpg" alt="">
    </swiper-slide>
  </swiper>
</template>
<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/css";

import "swiper/css/pagination";
import "swiper/css/navigation";

// import required modules
import { Autoplay, Pagination, Navigation } from "swiper";

export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    return {
      modules: [Autoplay, Pagination, Navigation],
    };
  },
};
</script>

<style scoped>
.mySwiper{
    width: 80%;
    height: 100%;
    overflow: hidden;
    border-radius: 1rem;
}
.mySwiper img{
    width: 100%;
    height: 100%;
}
</style>